@import url('https://fonts.cdnfonts.com/css/helvetica-neue-55');
@import url('https://db.onlinewebfonts.com/c/0d49fc455f4a8951a42daf952412a713?family=Helvetica+Neue+Regular');


#bedbible_penis_calculator {
	padding-top: 20px;
	padding-bottom: 20px;
	font-family: 'Helvetica Neue', sans-serif;
	font-family: "Helvetica Neue Regular";
	background-color: transparent;
	color: white !important;
	font-weight: 500;
	font-size: 20px;
	overflow: hidden;
	/*border: 1px solid #ff4d54;
	border-radius: 5px;
	-webkit-box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.53);
	-moz-box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.53);
	box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.53);*/
}

#bedbible_penis_calculator [type=number] {
  	height: 100%;
    max-width: 100%;
    text-align: center;
    font-family: inherit;
    font-size: 3.25rem;
    border: 0.5px solid white;
    border-radius: 2px;
    background: rgba(0,0,0,.7);
    color: white;
}

#length_block,
#girth_block,
#volume_block,
#result_text {
	align-items: center;
	padding: 18px 20px;
	vertical-align: middle !important;
	margin-bottom: 22px;
	border: 1px solid #fff;
	border-radius: 5px;
	-webkit-box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.2);
	-moz-box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.2);
	box-shadow: -2px 9px 32px 4px rgba(0,0,0,0.2);
}

#length_block {
	background-color: #5e7dba;
}

#girth_block {
	background-color: #5e7dba;
}

#volume_block {
	background-color: #b34d4d;
}

#result_text {
	text-align: center;
	background-color: #b34d4d;
	color: white;
}

#volume_block [type=number]  {
	border: 2px dotted white;
}

#bedbible_penis_calculator h3 {
	margin: 0 !important;
}

#length_block [type=number]:after {
    content: "qhjdjdhk";
    position: absolute;
    right: -10px;
    bottom: -10px;
    transform: rotate(315deg);
    text-shadow: 0 0 1px #999;
    color: #fff;
}

#length_block .afterElement::after,
#girth_block .afterElement::after {
  content: 'CM';
  position: absolute;
  color: white;
  transform: rotate(315deg);
  text-shadow: 0 0 3px #000;
	font-size: 1.75rem;
	margin-top: 24px;
  margin-left: -39px;       
}

#length_block .inches.afterElement::after,
#girth_block .inches.afterElement::after {
  content: 'IN'; 
  margin-top: 31px;
  margin-left: -25px;
}

#volume_block .afterElement::after {
  content: 'ML';
  position: absolute;
  color: white;
  transform: rotate(315deg);
  text-shadow: 0 0 3px #000;
  font-size: 1.75rem;
  margin-top: 24px;
  margin-left: -39px;       
}

#volume_block .flOz.afterElement::after {
	content: 'fl oz';  
	margin-top: 28px;
    margin-left: -45px;   
}

.final-percentage {
	font-size: 1.75rem;
	font-weight: 900;
	display: block;
	font-family: "Helvetica Neue Regular";
	color: white;
}

.final-percentage-text {
	font-family: "Helvetica Neue Regular";
	color: white;
	font-size: 1.2rem;
	font-weight: 600;
	display: block;
  	margin-top: -6px;
}

/* Result Block */

#switcher_block label {
	background-color: #ffdcdb;
  margin-right: 3px;
  padding: 10px 20px;
}

#switcher_block input[type="radio"]:checked + label.btn {
	background-color: #b34d4d !important;
	color: white ;
}


/* Remove arrows */
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/* MEDIA QUERY */
/* LAPTOP */
@media (min-width: 1400px) {}

/* small LAPTOP */
@media (max-width: 1024px){
	#bedbible_penis_calculator [type=number] {
		font-size: 2.8rem;
	}
	.final-percentage {
		font-size: 1.5rem;
	}
	.final-percentage-text {
		line-height: 1;
	}
	#length_block, 
	#girth_block, 
	#volume_block, 
	#result_text {
		padding: 18px 20px;
	}
}

/* TABLET */
@media (max-width: 768px){

	#bedbible_penis_calculator [type=number] {
    font-size: 3rem;
	}

	#length_block, #girth_block, #volume_block, #result_text {
    padding: 12px 18px;
	}

	#length_block .afterElement::after, #girth_block .afterElement::after {
		font-size: 1.5rem;
		margin-top: 28px;
		margin-left: -34px;
	}
	.final-percentage-text {
    	font-size: 1rem;
	}
	.final-percentage-text {
		font-size: 1rem;
	}
}

/* MOBILE */
@media (max-width: 475px) {

	#bedbible_penis_calculator h3 {
		font-size: 19px;
	}

	#result_text_bottom {
		font-size: 1.2rem;
	}

	.final-percentage {
		font-size: 18px;
	}
	.final-percentage-text {
		font-size: 14px;
	}
	#bedbible_penis_calculator [type=number] {
    	font-size: 2rem;
		padding: 5px 0;
	}
	#length_block .afterElement::after, 
	#girth_block .afterElement::after {
		font-size: 1rem;
		margin-top: 27px;
    	margin-left: -21px;
	}
	#volume_block .afterElement::after {
		font-size: 1rem;
		margin-top: 30px;
    	margin-left: -21px;
	}
	#length_block .inches.afterElement::after, 
	#girth_block .inches.afterElement::after {
		margin-top: 28px;
    	margin-left: -15px;
	}
	#volume_block .flOz.afterElement::after {
		margin-top: 30px;
		margin-left: -24px;
	}
	#length_block > .col-3,
	#girth_block > .col-3,
	#volume_block > .col-3 {
		padding-left: 2px !important;
		padding-right: 2px !important;
	}
	#length_block > div:nth-child(1),
	#girth_block > div:nth-child(1) {
		padding-left: 6px !important;
	}
	#volume_block > div:nth-child(1) {
		padding-left: 5px !important;
	}

}

@media (max-width: 375px) {
	#volume_block > div:nth-child(1) {
		padding-left: 3px !important;
	}
	#bedbible_penis_calculator h3 {
		font-size: 17px;
	}
	#result_text_bottom {
		font-size: 1.1rem;
	}
	#length_block, 
	#girth_block, 
	#volume_block, 
	#result_text {
		padding: 12px 5px;
	}
	#length_block > div.col-6.col-md-6.col-lg-6,
	#girth_block > div.col-6,
	#volume_block > div.col-6 {
		padding-left: 7px;
		padding-right: 3px;
	}
}

